<template>
  <div id="gallery">
    <a
      v-for="item in content"
      :key="item.id"
      :data-lg-size="item.size"
      :data-src="item.src"
    >
      <img
        className="img-responsive gallery-item"
        :src="item.src"
        :alt="item.name"
      />
    </a>
  </div>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import lightGallery from "lightgallery";

// Plugins
// eslint-disable-next-line no-unused-vars
import lgThumbnail from "lightgallery/plugins/thumbnail";
// eslint-disable-next-line no-unused-vars
import lgZoom from "lightgallery/plugins/zoom";

import imgRouter from "@/plugins/img-router.js";

import axios from "axios";

export default {
  name: "Gallery",
  components: {},
  data: () => ({
    content: [],
  }),
  mounted: function () {
    document.title = "Gallery";
    imgRouter.routeElements(document.getElementsByTagName("img"));

    const thiz = this;
    axios
      .get(`${process.env.BASE_URL}gallery_list.json`)
      .then((response) => {
        thiz.content = response.data;
      })
      .then(() => {
        // lightgallery(document.getElementsByClassName("gallery"));
        console.log(thiz.content);
        lightGallery(document.getElementById("gallery"), {
          plugins: [lgThumbnail, lgZoom],
          speed: 500,
          mode: "lg-fade",
          rowHeight: 300,
          margins: 5,
        });
      });
  },
  methods: {},
};
</script>

<style lang="css" scoped>
@import "lightgallery/css/lightgallery.css";
@import "lightgallery/css/lg-thumbnail.css";
@import "lightgallery/css/lg-zoom.css";
</style>

<style lang="less" scoped>
@import "@/assets/css/variables.less";
.gallery {
  max-width: "@{page-max-width}px";
}
.gallery-item {
  width: 300px;
  padding: 8px;
}
</style>
